@import url(https://fonts.googleapis.com/css2?family=Merriweather&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Baloo+Bhaijaan+2&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Merriweather&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: sans-serif;
}

body {
  margin: 0;
  padding: 0;
  background: #4b2d46;
}
body::-webkit-scrollbar{
  width: 5px;
}
body::-webkit-scrollbar-thumb{
  height: 5rem;
  border-radius: 1rem;
  background-color: #f1efef;
}
body::-webkit-scrollbar-track{
  background-color: #000;
}

.layout{
  display: flex;
  align-items: center;
  flex-direction: column;
  grid-row-gap: 1.25rem;
  row-gap: 1.25rem;
}

.noMeme{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.noMeme>a,a{
    text-decoration: none;
    font-size: 1.75rem;
    font-family: 'Merriweather';
    padding: 0.5em 1.25em;
    color: #fff;
    border: 2px solid #d2691e;
    transition: background 300ms ease;
}
.noMeme>a:hover,.noMeme>a:focus-visible{
    background: #d2691e;
}

h2{
    font-size: 3rem;
    color: #fff;
    text-align: center;
}

.myMemes{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
    grid-gap: 1rem;
    gap: 1rem;
}
.mymemeimages{
    width: 22rem;
    aspect-ratio: 1;
}
.loadingText{
    font-size: 2rem;
    text-align: center;
    color: #e7e7e7;
}
.signIn{
    color: #fff;
    font-size: 2rem;
    text-align: center;
}
.heading{
    font-size: 2rem;
    font-family: 'Roboto Slab', serif;
    background: #b896b3;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
}
.heading>a{
    text-decoration: none;
    font-size: 2rem;
    font-weight: 500;
    color: #302e8f;
    border: 2px solid currentColor;
    padding: 0.25rem 0.75rem;
    transition: all 500ms ease;
}
.heading>a:hover,.heading>a:focus-visible{
    color: #f7f7f7;
    background: #302e8f;
}
.newImage{
    height: 26rem;
    width: 22rem;
    border: 2px solid #90cf8e;
    padding: 1rem;
    cursor: pointer;
    transition: -webkit-transform 300ms ease;
    transition: transform 300ms ease;
    transition: transform 300ms ease, -webkit-transform 300ms ease;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}
.newImage > img{
    width: 100%;
}
.newImage div{
    font-family: 'Baloo Bhaijaan 2', cursive;
    color: #90cf8e;
    font-size: 1.5rem;
    line-height: 1.2;
}
.newImage:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
.wrapper{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    grid-gap: 2rem;
    gap: 2rem;
}
.wrapper img{
    height: 20rem;
    aspect-ratio: 1;
}
.btn{
    outline: none;
    border: none;
    width: 70%;
    border-radius: 0.5rem;
    font-size: 1.5rem;
    padding: 0.5rem 0.75rem;
    background: #d6abd0;
    cursor: pointer;
    font-weight: 600;
    font-family:'Merriweather', serif ;
    transition: background 350ms ease;
}
.btn:hover,.btn:focus-visible{
    background: #8bb887;
}
.cancel:hover{
    background: #e08181;
}
.images{
    height: 20rem;
    width: 25rem;
}
.makeNew{
    display: flex;
    justify-content: space-evenly;
    grid-gap: 1rem;
    gap: 1rem;
}
.newForm{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    grid-gap: 1rem;
    gap: 1rem;
}
.newForm > input{
    outline: none;
    border: none;
    width: 20rem;
    font-size: 1.25rem;
    font-family: 'Merriweather', serif;
    padding: 0.5rem 0.25rem;
    border-radius: 0.25rem;
}
.newForm > input:focus-visible{
    background: #eed0e9;
}
.savememDiv{
    text-align: center;
    width: 30rem;
}
.savememe{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
    grid-gap: 0.25rem;
    gap: 0.25rem;
}
